/*Start reset css */
* {
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: OpenSans;
    color: #a2a2a2;
    font-size: 15px;
}
h1, h2, h3, h4, h5, h6, p, blockquote, span {
    margin: 0;
}
ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
}
img {
    max-width: 100%;
}
a {
    color: inherit;
    text-decoration: none;
}
a:hover, a:active {
    text-decoration: none;
}
/* End reset css */